<template><section><html><head></head><body><h1>闪圆</h1>
<p><code v-pre="">vc-shine-ellipse</code> 组件用于加载闪圆效果，实质是通过 <code v-pre="">vc-entity</code> 和 <code v-pre="">vc-graphics-ellipse</code> 添加一个实体圆，然后不停修改圆的材质透明度实现。参考 <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a> 。</p>
<h2>示例</h2>
<h3>加载闪圆效果</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-shine-ellipse ref="ellipse" @ready="subReady" :position="position" color="red"></vc-shine-ellipse>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-shine-ellipse</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"ellipse"</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"subReady"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"red"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-shine-ellipse</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-tianditu</span> <span class="hljs-attr">map-style</span>=<span class="hljs-string">"img_c"</span> <span class="hljs-attr">token</span>=<span class="hljs-string">"436ce7e50d27eede2f2929307e6b33c0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-tianditu</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">117.217124</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.809777</span> }
      }
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-variable language_">window</span>.<span class="hljs-property">vm</span> = <span class="hljs-variable language_">this</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span> = cesiumInstance
      },
      <span class="hljs-title function_">subReady</span>(<span class="hljs-params">{cesiumObject}</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = <span class="hljs-variable language_">this</span>.<span class="hljs-property">cesiumInstance</span>
        viewer.<span class="hljs-property">scene</span>.<span class="hljs-property">globe</span>.<span class="hljs-property">depthTestAgainstTerrain</span> = <span class="hljs-literal">true</span>
        cesiumObject.<span class="hljs-property">createPromise</span>.<span class="hljs-title function_">then</span>(<span class="hljs-function">() =&gt;</span> {
          viewer.<span class="hljs-title function_">zoomTo</span>(viewer.<span class="hljs-property">entities</span>)
        })
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">required</code> 指定闪圆效果实体添加的位置。结构：{ lng: number, lat: number, height: number } 或者 Cesium.Cartesian3</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定闪圆效果是否可见。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">undefined</code></td>
<td><code v-pre="">optional</code> 指定闪圆效果效果椭圆高度。单位米。</td>
</tr>
<tr>
<td>radius</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定闪圆效果效果半径。单位米。</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre="">'white'</code></td>
<td><code v-pre="">optional</code> 指定闪圆效果颜色。</td>
</tr>
<tr>
<td>imageUrl</td>
<td>String</td>
<td><code v-pre="">''</code></td>
<td><code v-pre="">optional</code> 指定用于表达闪圆效果的材质图片。</td>
</tr>
<tr>
<td>deviationAlpha</td>
<td>Number</td>
<td><code v-pre="">0.05</code></td>
<td><code v-pre="">optional</code> 指定闪圆效果颜色 Alpha 值改变尺度，值越大闪得越快。取值范围: (0, 1)</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例, 以及闪圆实体实例。</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          position: { lng: 117.217124, lat: 31.809777 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          window.vm = this
          this.cesiumInstance = cesiumInstance
        },
        subReady({cesiumObject}) {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          cesiumObject.createPromise.then(() => {
            viewer.zoomTo(viewer.entities)
          })
        }
      }
    }
  </script>